import {mapState,mapMutations, mapActions, mapGetters} from 'vuex'
export default {
  props: {
    layout: {
      type: String,
      default: "card"
    }
  },
  data() {
    return {
      registerData: {
        email: "",
        name: "",
        password: "",
        
      },
      loginData: {
        email: "",
        password: "",
        
      },
      mode: "login",
    }
  },
  computed: {
    ...mapState(['menuState','auth'])
  },
  methods: {
    ...mapMutations(['setMenuState']),
    ...mapActions({
      register: 'auth/register',
      login: 'auth/login',
      logout: 'auth/logout',
      loginFacebook: "auth/loginFacebook",
      authInit: "auth/init"
    }),
    ...mapGetters({
      getUserPhoto: 'auth/getUserPhoto',
      isAdmin: 'auth/isAdmin'
    })
  },
  mounted(){
    // this.authInit()
  }
}



